import { useEffect } from 'preact/hooks';
import cdstyle from './style.css';
import CDHeader from '../header';
import CDFooter from '../footer';

const CDLayout = ({ children }) => {
  useEffect(() => {
    // Add Google Tag Manager script
    let noscript = document.createElement('noscript');
    noscript.setAttribute('id', 'gtmMTX');
    let iframe = document.createElement('iframe');
    iframe.src = 'https://www.googletagmanager.com/ns.html?id=GTM-MTXGV3F';
    iframe.height = '0';
    iframe.width = '0';
    iframe.style.display = 'none';
    iframe.style.visibility = 'hidden';
    noscript.appendChild(iframe);

    let isAlreadyAdded = document.getElementById('gtmMTX');
    if (!isAlreadyAdded) document.body.prepend(noscript);
  }, []);
  useEffect(() => {
    if (typeof window !== 'undefined') {
      if (window.location.pathname !== '/result/') {
        window.scrollTo({ top: 0, behavior: 'smooth' });
      }
      window.addEventListener('load', () => {
        window.dataLayer.push({ event: 'page_view',
      page_location: window.location.href })
      // Track page view with Google Analytics
      if(window.gtag){
        window.gtag({
          event: 'page_view',
          page_path: window.location.pathname,
        });
      }
      })
      return () => {
        window.removeEventListener('load', () => {})
      }
    }
  }, []);

  return (
      <div class={cdstyle.layout}>
        <CDHeader />
        <div class={cdstyle.wrapper}>{children}</div>
        <CDFooter />
      </div>
  );
};

export default CDLayout;
