import card from "../../assets/cdimages/visacd.webp";
import cardmaster from "../../assets/cdimages/mastercd.png";
import cdstyle from "./style.css";

const CDFooter = () => (
  <div class={cdstyle.cdfooter}>
    <div class={cdstyle.cdcontainer}>
      <p>
        Canada District is a private immigration company that specializes in
        helping people make a fresh start in Canada. We provide a range of
        services to support immigrants as they navigate their new lives in
        Canada. We will help you improve your language skills, search for jobs,
        fill out paperwork and guide you until you successfully complete your
        process.
      </p>
      <div class={cdstyle.cdfooterDetail}>
        <span>1-778-807-9840</span>
        <span>support@canadadistrict.com</span>
      </div>
      <p />
      <div class={cdstyle.cdfooterPay}>
        <img src={card} alt="card" width={40} height={40} />
        <img src={cardmaster} alt="card" width={40} height={40} />
      </div>
    </div>
  </div>
);

export default CDFooter;
